.App {
  background-color: #FDBB32;
  border-radius: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}
.title {
  width: 100%;
  text-align: center;
  font-size: 20px;
  margin: 10px 0 10px 0px;
}
.app-info {
  text-align: center;
  margin: 10px;
}
@media (min-width: 992px) {
  .App {
    width: 800px;
    padding: 10px;
    margin: 50px auto;
    -webkit-box-shadow: 0 14px 45px rgba(0, 0, 0, 0.22), 0 18px 45px rgba(0, 0, 0, 0.22);
    box-shadow: 0 14px 45px rgba(0, 0, 0, 0.22), 0 18px 45px rgba(0, 0, 0, 0.22);
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
  .title {
    margin: 0px 0 10px 0px;
  }
}



