.variation-view {
  display: -ms-flexbox;
  display: flex;
  min-height: 88px;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  background-color: #FDBB32;
  border-radius: 10px;
  padding: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.info {
  width: 50%;
  padding: 5px 5px 5px 10px;
  font-family: helvetica;
}
.bronze {
  color: #CD7F32;
}
.silver {
  color: #C9C0BB;
}
.gold {
  color: gold;
}
.exped {
  text-align: right;
}
.exped-text {
  display: block;
}
@media (min-width: 992px) {
  .exped-text {
    display: inline-block;
  }
}



